.App{
  display: grid;
  width: 100vw;

  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  
}
.songList{
  font-size: 1.2em;
  display: flex;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 15px;
}
.topSongs{
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.topSongs .item{
  font-size: 1.3em;
}
@media(max-width:800px)
{
  .App{
    display: flex;
    flex-direction: column-reverse;
  }
  .topSongs{
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
  }
}